* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  height: 100vh;
  overflow-x: hidden;
}

.overpass {
  font-family: Overpass;
}

.ubuntu {
  font-family: Ubuntu;
}

.flexCenterAll {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.flexCenterAll__row {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.py-1 {
  padding: 1rem 0;
}

.my-1 {
  margin: 1rem 0;
}

.mt-1 {
  margin-top: 1rem;
}

.borderTop {
  border-top: 1px solid #4b586233;
}

.white {
  color: #fff;
}

.text-center {
  text-align: center;
}

.Uy5SBW_header {
  background: url("../images/bg-pattern-intro-desktop.06dcf942.svg") center no-repeat, linear-gradient(to bottom right, #ff8f70e6, #ff3d54f7);
  border-bottom-left-radius: 6rem;
}

.Uy5SBW_footer {
  text-align: center;
  background: #25252d;
  border-top-right-radius: 6rem;
}

.GzISqW_menu {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.GzISqW_menu .GzISqW_nav-links {
  width: 100%;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 2rem;
  display: flex;
}

.GzISqW_menu .GzISqW_nav-links a {
  color: #fff;
}

.GzISqW_menu .GzISqW_nav-links a:after {
  content: url("../images/icon-arrow-light.0ea96185.svg");
  margin-bottom: 0;
}

.GzISqW_menu .GzISqW_nav-links a:hover {
  text-decoration: underline;
}

.GzISqW_menu .GzISqW_nav-links ul {
  width: 10.375rem;
  background: #fff;
  position: absolute;
  top: 85%;
}

.GzISqW_menu .GzISqW_nav-links ul li a {
  color: #6e7176;
}

.GzISqW_menu .GzISqW_nav-links ul li a:hover {
  color: #2a3642;
  font-weight: bold;
  text-decoration: none;
}

.GzISqW_menu .GzISqW_account {
  border: unset;
  flex-direction: row;
  gap: 2.5rem;
  display: flex;
}

.GzISqW_menu .GzISqW_account a {
  color: #fff;
}

.GzISqW_menu .GzISqW_account button {
  color: #ff3d54;
  background: #fff;
}

.GzISqW_menu .GzISqW_account button:hover {
  color: #fff;
  background: #ff7a85;
}

